/* eslint-disable no-unused-expressions */
import {
  ApolloError,
  DocumentNode,
  gql,
  QueryHookOptions,
  useQuery,
} from '@apollo/client';
import {
  AtomButton,
  AtomInput,
  AtomLoader,
  AtomText,
  AtomWrapper,
} from '@atoms';
import { TDStyled } from '@atoms/AtomTable/style';
import { TRStyled } from '@atoms/AtomTableQuery/styled';
import { ParamsTypes } from '@atoms/AtomTableQuery/types';
import { css, SerializedStyles } from '@emotion/react';
import { RootStateType } from '@Redux/reducer';
import { FormikValues } from 'formik';
import { useEffect, useMemo, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import {
  InputErrorStyled,
  InputSelectPaginationLabelStyled,
  InputTBodyStyled,
  InputTextSpanStyled,
} from './style';
import { AtomInputTypes } from './types';

// const Animation = {
