//   transition: {
//     default: { duration: 0.3 },
//   },
//   initial: { opacity: 0 },
//   animate: { opacity: 1 },
//   exit: { opacity: 0 },
// };

const QUERYDEFAULT = gql`
  query ListUsers(
    $skip: Int
    $take: Int
    $filter: MemberFilterInput
    $order: [MemberSortInput!]
  ) {
    listUsers(skip: $skip, take: $take, filter: $filter, order: $order) {
      pageInfo {
        hasNextPage
        hasPreviousPage
        __typename
      }
      totalCount
      items {
        firstTime
        email
        id
        profile {
          id
          firstName
          lastName
          __typename
        }
        groups {
          id
          name
          __typename
        }
        memberRoles {
          companyId
          roleId
          role {
            name
            codeName
            description
            __typename
          }
          id
          createdAt
          updatedAt
          __typename
        }
        memberProjects {
          id
          memberId
          memberProjectRoleProject {
            projectRole {
              id
              name
              codeName
              __typename
            }
            __typename
          }
          __typename
        }
        createdAt
        lastLogin
        __typename
      }
      __typename
    }
  }
`;

type AtomInputSelectTypes = {
  label?: string;
  labelWidth?: string;
  labelHeight?: string;
  labelTextAlign?: string;
  labelMargin?: string;
  labelColor?: string;
  labelPadding?: string;
  labelFontSize?: string;
  labelFontWeight?: string | number;
  labelFontFamily?: string;
  spanMargin?: string;
  customCSS?: SerializedStyles;
  placeholderColor?: string;
  placeholder?: string;
  defaultText?: string;
};

export type AtomInputSelectColumnTypes<T extends object> = {
  key?: string;
  sortable?: boolean;
  searchable?: boolean;
  view: (data?: T) => JSX.Element;
  css?: (data?: T) => SerializedStyles;
};

type AtomInputSelectPagination<
  T extends object,
  P extends object
> = AtomInputSelectTypes & {
  columns: AtomInputSelectColumnTypes<T>[];
  QUERY: DocumentNode;
  data: (data: P) => T[];
  onError?: (error: ApolloError) => void;
  onCompleted?: (data: P) => void;
  options?: QueryHookOptions;
  onChange?: (value: string, data: T[]) => T[];
  onPress: (data?: T) => void;
  id?: string;
  formik?: FormikValues;
  positionBox: 'top' | 'bottom';
  maxHeightBox: string;
  error?: string;
  errorColor?: string;
  errorFontSize?: string;
  errorFontWeight?: string;
  errorFontFamily?: string;
  errorTextAlign?: string;
  errorMargin?: string;
  errorPadding?: string;
  errorHeight?: string;
};

const InputSelectWtihPagination = <T extends object, P extends object>(
  props: AtomInputSelectPagination<T, P>
) => {
  const { onChange, columns, placeholder, onPress } = props;
  const customize = useSelector((state: RootStateType) => state.customize);
  const {
    labelWidth,
    labelColor,
    labelFontFamily,
    id,
    labelFontSize,
    labelFontWeight,
    formik,
    labelMargin,
    maxHeightBox,
    error: ErrorCustom,
    data,
    positionBox,
    onError,
    labelPadding,
    spanMargin,
    onCompleted,
    QUERY,
    customCSS,
    label,
    options,
    defaultText,
  } = props;
  const [skip, setSkip] = useState(0);
  const [getData, setData] = useState<T[]>([]);
  const [searchData, setsearchData] = useState('');
  const [isOpen, setOpen] = useState(false);
  const [getParams, setParams] = useState({} as ParamsTypes);
  const scrollRef = useRef<HTMLDivElement>();
  const getFilter = useMemo(() => {
    if (options?.variables?.filter) {
      return {
        filter: {
          ...options?.variables?.filter,
          // ...getSearch?.filter,
        },
      };
    }
    return null;
  }, [getParams, options]);

  const take = options?.variables?.take ?? 50;

  const QUERYGET = useQuery<P>(QUERY ?? QUERYDEFAULT, {
    ...options,
    variables: {
      ...options?.variables,
      ...getFilter,
      skip: take * skip,
      take,
    },
    onCompleted: (e) => {
      onCompleted?.(e);
    },
    onError: (error) => {
      onError?.(error);
    },
  });

  useEffect(() => {
    const get = Object.values(QUERYGET?.data ?? ({} as ParamsTypes))?.[0];

    const params = {
      pageInfo: get?.pageInfo ?? getParams?.pageInfo,
      totalCount: get?.totalCount ?? getParams?.totalCount,
    } as ParamsTypes;

    const dataTable = data?.(QUERYGET?.data) ?? getData;
    setParams(params);
    setData(dataTable);
  }, [QUERY, options, QUERYGET]);

  return (
    <InputSelectPaginationLabelStyled
      labelWidth={labelWidth}
      labelColor={labelColor}
      labelFontFamily={labelFontFamily}
      labelFontSize={labelFontSize}
      labelFontWeight={labelFontWeight}
      labelMargin={labelMargin}
      labelPadding={labelPadding}
      customCSS={customCSS}
    >
      {label && (
        <InputTextSpanStyled spanMargin={spanMargin}>
          {label}
        </InputTextSpanStyled>
      )}
      <AtomWrapper
        customCSS={css`
          display: grid;
          grid-template-columns: 1fr auto;
        `}
      >
        <AtomWrapper
          customCSS={css`
            position: relative;
            flex-direction: ${positionsBox[positionBox]};
            flex: 1;
            gap: 8px;
          `}
        >
          <AtomWrapper
            customCSS={css`
              display: flex;
              flex-direction: row;
              align-items: center;
              justify-content: space-between;
              background-color: white;
              /* padding: 10px; */
              border: 1px solid #0000003f;
            `}
          >
            <AtomWrapper alignItems="center" justifyContent="center">
              <AtomInput
                type="text"
                value={searchData}
                placeholder={placeholder}
                defaultText={defaultText}
                onChange={(event) => {
                  setsearchData(event.target?.value);
                  const result =
                    (onChange &&
                      onChange(event.target?.value, data?.(QUERYGET?.data))) ??
                    data?.(QUERYGET?.data);

                  setData(
                    event.target?.value === '' ? data?.(QUERYGET?.data) : result
                  );
                }}
                // onClick={() => setOpen(true)}
                customCSS={css`
                  width: 100%;
                  padding: 0px;
                  padding-left: 10px;
                  input {
                    padding: 0px;
                    background-color: transparent;
                    box-shadow: transparent;
                    border: transparent;
                  }
                `}
              />
            </AtomWrapper>
            <AtomButton
              width="auto"
              onClick={() => {
                setOpen((prev) => !prev);
              }}
              backgroundColor="transparent"
              customCSS={css`
                padding: 0px 10px;
              `}
            >
              <AtomWrapper
                customCSS={css`
                  width: auto;
                  border: 0.5px solid black;
                  border-width: 0 2px 2px 0;
                  display: inline-block;
                  padding: 5px;
                  ${isOpen
                    ? css`
                        margin-top: 15px;
                        transform: rotate(-135deg);
                        -webkit-transform: rotate(-135deg);
                      `
                    : css`
                        transform: rotate(45deg);
                        -webkit-transform: rotate(45deg);
                      `}
                `}
              />
            </AtomButton>
          </AtomWrapper>
          <AtomWrapper position="relative">
            {isOpen && (
              <>
                <AtomWrapper
                  customCSS={css`
                    background-color: white;
                    ::placeholder {
                      color: #202124;
                    }
                    border: 1px solid #f2f2f2;
                    position: absolute;
                    ${moveBox[positionBox ?? 'bottom']}
                    ::-webkit-scrollbar-thumb {
                      background: ${customize?.colors?.primary || '#f0a58b'};
                    }
                    overflow: ${QUERYGET?.loading ? 'hidden' : 'scroll'};
                    overflow-x: hidden;
                    width: 100%;
                    max-height: ${maxHeightBox ?? '420px'};
                    align-items: center;
                    justify-content: flex-start;
                    border: 1px solid #0000003f;
                  `}
                  refObject={scrollRef}
                  onScroll={() => {
                    if (scrollRef.current) {
                      const top = scrollRef?.current?.scrollTop === 0;
                      const bottom =
                        scrollRef?.current?.scrollTop +
                          scrollRef?.current?.clientHeight ===
                        scrollRef?.current?.scrollHeight;

                      if (top && !QUERYGET?.loading) {
                        setSkip(
                          getParams?.pageInfo?.hasPreviousPage ? skip - 1 : skip
                        );
                      }

                      if (bottom && !QUERYGET?.loading) {
                        setSkip(
                          getParams?.pageInfo?.hasNextPage ? skip + 1 : skip
                        );
                      }
                    }
                  }}
                >
                  {QUERYGET?.loading ? (
                    <AtomLoader
                      type="small"
                      colorLoading={customize?.colors?.primary}
                      isLoading
                    />
                  ) : (
                    <AtomWrapper alignItems="center" justifyContent="center">
                      <InputTBodyStyled>
                        {getData?.map((e, i) => (
                          <TRStyled
                            key={`row${i + 1}`}
                            animate={{
                              opacity: 1,
                            }}
                            initial={{ opacity: 0 }}
                            transition={{
                              delay: i * 0.03,
                              duration: 0,
                              ease: 'easeOut',
                            }}
                          >
                            {columns?.map((td, j) => (
                              <TDStyled
                                key={`cell ${i + 1} ${j + 1}`}
                                {...td}
                                title={`${td?.key} ${i + 1}`?.toUpperCase()}
                                onClick={() => {
                                  onPress(e);
                                  setOpen(false);
                                }}
                              >
                                {td.view(e)}
                              </TDStyled>
                            ))}
                          </TRStyled>
                        ))}

                        {getData?.length ? null : (
                          <AtomText>{getData?.length} Resultados</AtomText>
                        )}
                      </InputTBodyStyled>
                    </AtomWrapper>
                  )}
                </AtomWrapper>
              </>
            )}
          </AtomWrapper>
        </AtomWrapper>
      </AtomWrapper>
      {formik?.errors?.[id] ?? ErrorCustom ? (
        <InputErrorStyled {...(props as unknown as AtomInputTypes)}>
          {formik?.errors?.[id] ?? ErrorCustom}
        </InputErrorStyled>
      ) : null}
    </InputSelectPaginationLabelStyled>
  );
};

const moveBox = {
  bottom: css`
    top: 0;
  `,
  top: css`
    bottom: 0;
  `,
};

const positionsBox = {
  bottom: 'column',
  top: 'column-reverse',
};

export default InputSelectWtihPagination;
